.why {
  font-size: 14px;
  width: 80%;
}

hr {
  width: 100%;
  height: 2px;
  float: left;
}

@media screen and (max-width: 800px) {
  .paragraph {
    float: left;
  }
}

.email_link {
  display: inline;
  font-size: 30px;
}

.underline {
  text-decoration: none;
  border-bottom: 3px solid #6495ed;
}

fieldset {
  border: 1px solid #006c90;
  width: 325px;
  background: #e0997e;
}

.form-group {
  margin-top: 25px;
}

.normal {
  float: left;
  width: 355px;
  margin-right: 10px;
  margin-top: 5px;
  margin-bottom: 20px;
  background-color: #c6c6c6;
}

.all {
  float: left;
  width: 355px;
  margin-top: 5px;
  margin-bottom: 20px;
  background-color: #e0e0e0;
}

.all > p {
  font-size: 14px;
}

.normal > p {
  font-size: 14px;
}

.page-header {
  margin-left: 0;
}

article {
  width: 100%;
  max-width: 1000px;
  margin-top: 20px;
  margin-left: 0;
  margin-bottom: 20px;
}

.paragraph {
  width: 100%;
  max-width: 800px;
  text-align: left;
  text-justify: none;
  margin-right: 10px;
  margin-left: 0;
}

.post {
  font-size: 14px;
}

.footnote {
  display: block;
}

.prettyprint {
  font-size: 14px;
  width: 350px;
  background: #ffffff;
  float: left;
  margin-right: 15px;
}

.border {
  border: 1px solid black;
}

.lb-data .lb-caption {
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
}

.google-cse-container {
  max-width: 600px;
  padding: 0;
}

pre {
  float: left;
}

.chess-column {
  width: 400px;
  float: left;
}
