nav {
  height: 50px;
  grid-row: 1;
  grid-column: 1/8;
  position: sticky;
  top: 0px;
  background-color: #e3e4e6;
  z-index: 100;
}

.nav_wrap {
  margin: 0;
  width: 350px;
}

.home {
  padding-right: 10px;
  padding-left: 4px;
  padding-top: 0;
  padding-bottom: 0;
}

nav ul {
  margin: 0;
  list-style: none;
  padding: 0;
}

nav ul li {
  position: relative;
  float: left;
  z-index: 25;
}

nav ul li a {
  display: block;
  color: black;
  float: left;
  font-size: 16px;
  padding-top: 12px;
  padding-right: 6px;
  padding-bottom: 12px;
  padding-left: 6px;
  text-decoration: none;
}

nav ul li a:hover {
  background: #f1c232;
  color: #000;
}

@media only screen and (min-width: 600px) {
  nav ul li a {
    float: left;
    font-size: 18px;
    padding: 12px;
  }

  .nav_wrap {
    margin: 0 auto;
    width: 500px;
  }
}
